import { VaultApyAndFeesPath } from '@/apis/path'
import { useFetch } from '@cetus/hooks'
import { formatTimestamp } from '@cetus/utils'
import { identity, pickBy } from 'lodash-es'

export type GetVaultApyAndFeesHistogramParams = {
  vaultID: string
  beginTimestamp: number | string
  endTimestamp: number | string
}

// Ëé∑ÂèñVaultÁöÑapyÂíåfeesÁöÑhistogramÊï∞ÊçÆ
export default function useGetVaultApyAndFeesHistogram() {
  const { fetchByApi } = useFetch()

  const getVaultApyAndFeesHistogram = async (params: GetVaultApyAndFeesHistogramParams) => {
    console.log('üöÄ ~ getVaultHistogram ~ params:', params)
    const requestParams: any = { ...params }
    requestParams['dateType'] = 'hour'
    try {
      const options = pickBy(requestParams, identity)
      const res = await fetchByApi(VaultApyAndFeesPath, 'GET', options)
      console.log('üöÄ ~ getVaultApyAndFeesHistogram ~ res:', res)

      if (!res?.list) return []

      const list = res?.list.map((item: any, index: number) => {
        const timestampMs = item.Timestamp * 1000
        const date = new Date(timestampMs)
        const monthIndex = Number(date.toISOString().slice(5, 7)) // Ëé∑ÂèñÊúà‰ªΩ

        return {
          apy: +item.value.apy * 100, // Âø´ÈÄüËΩ¨Êç¢‰∏∫Êï∞Â≠ó
          lp_fee: +item.value.lp_fee, // Âø´ÈÄüËΩ¨Êç¢‰∏∫Êï∞Â≠ó
          date: item.Timestamp,
          tooltipTime: formatTimestamp(timestampMs)
        }
      })
      return {
        list,
        quote_type: res?.quote_type
      }
    } catch (error) {
      console.error('üöÄ ~ useGetHistogramData ~ getHistogramData Error:', error)
      return []
    }
  }

  return { getVaultApyAndFeesHistogram }
}
