import { VaultV2ApiInfo } from '@/types/vaultsV2'
import { AddressCopyLink, Block, CetusTooltip, TooltipIcon } from '@cetus/design'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { CoinPairImage, HTextLabelBox } from '@cetus/ui-kit'
import { symbolDataDisplayProcessing } from '@cetus/utils'
import { Center, HStack, Stack, Text, VStack } from '@chakra-ui/react'
import PoweredByHaedal from '../common/PoweredByHaedal'
import { VaultsAprTooltipContent } from '../list/common/VaultsAprBlock'
import { CoinImage } from '../list/common/VaultsRewardsBlock'

type VaultsHeaderProps = {
  apiVaultInfo: VaultV2ApiInfo
  vaultTvl: string
  performanceFee: string
}

export function VaultsHeader({ apiVaultInfo, vaultTvl, performanceFee }: VaultsHeaderProps) {
  const { isApp } = useWindowWidth()
  const { getExplorerUrl } = useExplorer()
  return (
    <Stack flexDir={{ base: 'column', lg: 'row' }} w="100%" justifyContent="space-between">
      {/* Â∑¶ËæπÂå∫Âüü */}
      <HStack>
        <CoinPairImage
          coinACoinType={apiVaultInfo?.displayTokenA?.coin_type}
          coinBCoinType={apiVaultInfo?.displayTokenB?.coin_type}
          coinAIconUrl={apiVaultInfo?.displayTokenA?.logo_url}
          coinBIconUrl={apiVaultInfo?.displayTokenB?.logo_url}
        />

        <VStack alignItems="start" flexDirection={apiVaultInfo?.category == 'haedal' ? 'column' : 'row'} gap="2px">
          <HStack>
            <Text fontSize="16px" color="text_caption">
              {`${apiVaultInfo?.displayTokenA?.symbol || ''} - ${apiVaultInfo?.displayTokenB?.symbol || ''}`}
            </Text>
            <Block w="unset" p="4px 6px" borderRadius="12px" ml="4px">
              <HStack>
                <Text fontSize="12px" color="primary">
                  {apiVaultInfo?.feeDisplay}
                </Text>
              </HStack>
            </Block>
          </HStack>
          <HStack mt="4px">
            {apiVaultInfo?.category == 'haedal' && <PoweredByHaedal fontSize="14px" imgW="20px" imgH="16px" category="haedal" />}
          </HStack>
        </VStack>
        {!isApp && (
          <HStack ml="12px" gap="4px">
            <Text fontSize="14px">Vault Address</Text>
            <AddressCopyLink
              fontSize="14px"
              address={apiVaultInfo?.vaultId || ''}
              onClickLink={() => {
                window.open(getExplorerUrl(apiVaultInfo?.vaultId, 'poolAddress'), '_blank')
              }}
            />
          </HStack>
        )}
      </HStack>
      {/* Âè≥ËæπÂå∫Âüü */}
      <Stack flexDir={{ base: 'column', lg: 'row' }} gap={{ base: '8px', lg: '60px' }}>
        {isApp && (
          <HTextLabelBox
            label="Vault Address"
            labelStyle={{
              fontSize: '14px'
            }}
            value={
              <AddressCopyLink
                fontSize="14px"
                color="text_caption"
                address={apiVaultInfo?.vaultId || ''}
                onClickLink={() => {
                  window.open(getExplorerUrl(apiVaultInfo?.vaultId, 'poolAddress'), '_blank')
                }}
              />
            }
          />
        )}
        <VStack w={{ base: '100%', lg: 'unset' }} justify="space-between" gap="6px" flexDirection={{ base: 'row', lg: 'column' }}>
          <Text w="100%" fontSize={{ base: '14px', lg: '12px' }} textAlign={isApp ? 'left' : 'right'}>
            APY
          </Text>
          <CetusTooltip
            placement="bottom"
            tooltip={
              <VaultsAprTooltipContent
                vaultsLstApyDisplay={apiVaultInfo?.vaultsLstApyDisplay}
                vaultsApyDisplay={apiVaultInfo?.vaultsApyDisplay}
                vaultsTotalApyDisplay={apiVaultInfo?.vaultsTotalApyDisplay}
                vaultsAprDisplay={apiVaultInfo?.vaultsAprDisplay}
                category={apiVaultInfo?.category}
              />
            }
          >
            <Center>
              <Text
                color="text_highlight"
                h="20px"
                cursor="help"
                textDecoration="underline dotted"
                textUnderlineOffset="3px"
                lineHeight="20px"
                fontWeight="500"
              >
                {apiVaultInfo?.category == 'cetus' ? apiVaultInfo?.vaultsTotalApyDisplay : apiVaultInfo?.vaultsApyDisplay || '-'}
              </Text>
            </Center>
          </CetusTooltip>
        </VStack>
        <VStack w={{ base: '100%', lg: 'unset' }} justify="space-between" gap="6px" flexDirection={{ base: 'row', lg: 'column' }}>
          <Text w="100%" fontSize={{ base: '14px', lg: '12px' }} textAlign={isApp ? 'left' : 'right'}>
            Earnings
          </Text>
          <CetusTooltip
            placement="top"
            tooltip={
              <Text fontSize="12px" lineHeight="20px" maxW="240px">
                Rewards will be auto harvested and compounded to your position.
              </Text>
            }
          >
            <Center>
              <HStack justifyContent="end">
                {apiVaultInfo?.vaultsRewards.map(coinType => {
                  return <CoinImage key={coinType} coinType={coinType} />
                })}
              </HStack>
            </Center>
          </CetusTooltip>
        </VStack>
        <VStack w={{ base: '100%', lg: 'unset' }} justify="space-between" gap="6px" flexDirection={{ base: 'row', lg: 'column' }}>
          <Text w="100%" fontSize={{ base: '14px', lg: '12px' }} textAlign={isApp ? 'left' : 'right'}>
            TVL
          </Text>
          <Text color="text_caption" h="20px" lineHeight="20px" fontWeight="500">
            {(apiVaultInfo?.category == 'haedal' ? symbolDataDisplayProcessing(vaultTvl, '$') : apiVaultInfo?.vaultsTvlDisplay) || '-'}
          </Text>
        </VStack>
        {apiVaultInfo?.category == 'haedal' && (
          <VStack
            w={{ base: '100%', lg: 'unset' }}
            justify="space-between"
            align={{ base: 'center', lg: 'flex-end' }}
            gap="6px"
            flexDirection={{ base: 'row', lg: 'column' }}
          >
            <HStack gap="4px">
              <Text w="100%" fontSize={{ base: '14px', lg: '12px' }}>
                Performance Fee
              </Text>
              <TooltipIcon type="vaultDetailFee" />
            </HStack>
            <Text color="text_caption" h="20px" lineHeight="20px" fontWeight="500">
              {performanceFee || '-'}
            </Text>
          </VStack>
        )}
      </Stack>
    </Stack>
  )
}
