import useGetPythLastPrice from '@/hooks/vault-v2/pyth-price/useGetPythLastPrice'
import useCurrentVaultDetail from '@/hooks/vault-v2/useCurrentVaultDetail'
import useVaultAvaiableCapacity from '@/hooks/vault-v2/useVaultAvaiableCapacity'
import useVaultHoadings from '@/hooks/vault-v2/useVaultsHoldings'
import useSwapWidgetStore from '@/store/swap-widget/swapWidget'
import useVaultsActionStore from '@/store/vaults-v2/useVaultsAction'
import useVaultsListV2Store from '@/store/vaults-v2/useVaultsList'
import useVaultsPoolStore from '@/store/vaults-v2/useVaultsPool'
import useVaultsPositionStore from '@/store/vaults-v2/useVaultsPosition'
import { SelectTab } from '@cetus/design'
import { useAccountBalance, useInterval } from '@cetus/hooks'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { useAccountStore } from '@cetus/stores'
import envConfigs from '@cetus/types/src/config/envConfigs'
import { BackButton, NoData } from '@cetus/ui-kit'
import { formatNumber, isAvailableObject } from '@cetus/utils'
import { Box, Button, HStack, Stack, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useRef, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import ApyAndFeesChartBlock from './chart/ApyAndFeesChartBlock'
import PriceRangeChartPageBlock from './chart/PriceRangeChartPageBlock'
import TvlChartPageBlock from './chart/TvlChartPageBlock'
import { MyLiquidity } from './MyLiquidity'
import { PositionRange } from './PositionRange'
import VaultsAction from './VaultsAction'
import { VaultsComposition } from './VaultsComposition'
import { VaultsHeader } from './VaultsHeader'

export default function VaultsDetailPage() {
  const navigate = useNavigate()
  const { vaultId, category = 'cetus' } = useParams()
  const { getCurrentVaultDetail } = useCurrentVaultDetail()
  const { currentAccount } = useAccountStore()
  const { clearVaultsActionData } = useVaultsActionStore()
  const { currentVaultPosition, currentVaultPositionLoading, clearVaultsPositionObj, setCurrentVaultPosition, setCurrentVaultPositionLoading } =
    useVaultsPositionStore()
  const { vaultsPoolObj } = useVaultsPoolStore()
  const { vaultListObj, lpTokenInfoObj, vaultsList } = useVaultsListV2Store()
  const { fetchAccountBalance } = useAccountBalance()
  const { getCurrentVaultContractInfo } = useCurrentVaultDetail()
  const [isAvailableVaultId, setIsAvailableVaultId] = useState(true)
  const { setFromCoin, setToCoin } = useSwapWidgetStore()

  const currentVaultPool = useMemo(() => {
    return vaultsPoolObj[vaultId as string]
  }, [vaultsPoolObj, vaultId])

  useEffect(() => {
    if (vaultId) {
      getCurrentVaultDetail(vaultId as string)
    }
  }, [currentAccount?.address, vaultId])

  // useEffect(() => {
  //   getCurrentVaultContractInfo(vaultId as string)
  // }, [vaultId])

  const currentVaultPositionInfo = useMemo(() => {
    return currentVaultPosition
  }, [currentVaultPosition, vaultId])

  useEffect(() => {
    if (!currentAccount?.address) {
      clearVaultsPositionObj()
      setCurrentVaultPosition({})
    }
  }, [currentAccount?.address])

  useEffect(() => {
    if (vaultId && isAvailableObject(vaultListObj)) {
      const res = Object.values(vaultListObj)?.filter((item: any) => {
        return item.vaultId == vaultId
      })
      if (res?.length > 0) {
        console.log('üöÄ ~ res ~ res:', res)
        setIsAvailableVaultId(true)
      } else {
        setIsAvailableVaultId(false)
      }
    }
  }, [vaultId, vaultListObj])

  const apiVaultInfo = useMemo(() => {
    if (isAvailableObject(vaultListObj) && vaultId) {
      return vaultListObj[vaultId]
    }
    return
  }, [vaultListObj, vaultId])

  const lpTokenInfo = useMemo(() => {
    return lpTokenInfoObj[apiVaultInfo?.lpTokenType]
  }, [apiVaultInfo?.lpTokenType, JSON.stringify(lpTokenInfoObj)])

  const vaultTabList = [
    {
      label: 'Overview',
      value: 'Overview'
    },
    {
      label: 'Analytics',
      value: 'Analytics'
    }
  ]

  const [vaultCurrTab, setVaultCurrTab] = useState('Overview')

  const { holdingAmountDisplay, holdCoinAValue, holdCoinBValue } = useVaultHoadings(
    currentVaultPosition?.displayAmountA,
    currentVaultPosition?.displayAmountB,
    currentVaultPosition?.displayCoinTypeA,
    currentVaultPosition?.displayCoinTypeB,
    category as string
  )

  const {
    holdingAmount: vaultTvl,
    holdCoinAValue: poolHoldCoinAValue,
    holdCoinBValue: poolHoldCoinBValue
  } = useVaultHoadings(
    currentVaultPool?.displayAmountA,
    currentVaultPool?.displayAmountB,
    currentVaultPool?.displayCoinTypeA,
    currentVaultPool?.displayCoinTypeB,
    category as string
  )

  const [chartRefresh, setChartRefresh] = useState(false)
  const { getPythLastPrice } = useGetPythLastPrice()

  useInterval({
    interval: 20 * 1000,
    callback: () => {
      if (isMountedRef.current && category == 'haedal') {
        getPythLastPrice([currentVaultPool?.displayCoinTypeA, currentVaultPool?.displayCoinTypeB])
      }
    }
  })

  const isMountedRef = useRef(false)

  useEffect(() => {
    isMountedRef.current = true
    clearVaultsActionData()
    return () => {
      isMountedRef.current = false
      clearVaultsActionData()
    }
  }, [])

  const autoRefresh = () => {
    console.log('üöÄüöÄüöÄ ~ VaultsDetailPage.tsx:142 ~ autoRefresh ~ autoRefresh:')
    // Âà∑Êñ∞‰ΩôÈ¢ù
    fetchAccountBalance()
    console.log('üöÄüöÄüöÄ ~ VaultsDetailPage.tsx:157 ~ autoRefresh ~ currentAccount?.address:', currentAccount?.address)
    if (currentAccount?.address) {
      getCurrentVaultDetail(vaultId as string, true)
    } else {
      getCurrentVaultContractInfo(vaultId as string)
    }

    if (vaultCurrTab !== 'Overview') {
      setChartRefresh(true)
      setTimeout(() => {
        setChartRefresh(false)
      }, 1000)
    }
  }

  const avaiableCapacityInfo = useVaultAvaiableCapacity(currentVaultPool)

  const { isApp } = useWindowWidth()

  useEffect(() => {
    if (apiVaultInfo) {
      setFromCoin(apiVaultInfo?.displayTokenA)
      setToCoin(apiVaultInfo?.displayTokenB)
    }
    return () => {
      setFromCoin(envConfigs.clmm_swap.from_coin)
      setToCoin(envConfigs.clmm_swap.to_coin)
    }
  }, [apiVaultInfo?.vaultsId])

  return (
    <Box pt="40px" w="100%">
      {isAvailableVaultId ? (
        <VStack gap="20px" w="100%" align="flex-start">
          <HStack w="100%" justify="space-between">
            <BackButton bg="bg_six" borderColor="border_secondary" />
          </HStack>
          {/* Â§¥ÈÉ®Âå∫Âüü */}
          <VaultsHeader apiVaultInfo={apiVaultInfo} vaultTvl={vaultTvl} performanceFee={currentVaultPool?.disPlayProtocolFeeRate} />
          <Stack flexDir={{ base: 'column-reverse', lg: 'row' }} w="100%" justifyContent="space-between" gap="16px" alignItems="start">
            {/* Â∑¶ËæπÂå∫Âüü */}
            <VStack width="100%" flex="1" gap="12px">
              {apiVaultInfo?.category == 'haedal' && (
                <SelectTab
                  type="borderTab"
                  wrapStyle={{
                    w: '100%',
                    h: '60px',
                    padding: '20px',
                    bg: 'bg_six',
                    borderColor: 'border_secondary'
                  }}
                  itemStyle={{
                    fontSize: '16px',
                    mr: '28px'
                  }}
                  tabList={vaultTabList}
                  currentTab={vaultCurrTab}
                  handleChangeTab={(item: any) => {
                    setVaultCurrTab(item?.value)
                  }}
                />
              )}

              {vaultCurrTab == 'Overview' ? (
                <>
                  {/* ‰ª∑Ê†ºÂå∫Èó¥ */}
                  <PositionRange
                    minPrice={currentVaultPool?.minPrice}
                    maxPrice={currentVaultPool?.maxPrice}
                    currentPrice={currentVaultPool?.currentPrice}
                    displayTokenB={apiVaultInfo?.displayTokenA}
                    displayTokenA={apiVaultInfo?.displayTokenB}
                  />
                  {/* Ê±†Â≠ê token ÂàÜÈÖç */}
                  <VaultsComposition
                    displayAmountA={currentVaultPool?.displayAmountA}
                    displayAmountB={currentVaultPool?.displayAmountB}
                    displayPercentA={currentVaultPool?.displayPercentA}
                    displayPercentB={currentVaultPool?.displayPercentB}
                    displayTokenB={apiVaultInfo?.displayTokenB}
                    displayTokenA={apiVaultInfo?.displayTokenA}
                    vaultsCoinAValue={currentVaultPool?.displayAmountA ? poolHoldCoinAValue : ''}
                    vaultsCoinBValue={currentVaultPool?.displayAmountB ? poolHoldCoinBValue : ''}
                    depositRatio={avaiableCapacityInfo?.depositRatio}
                    hardCapUSD={avaiableCapacityInfo?.hardCapUSD}
                    vaultTvl={vaultTvl}
                  />
                  {/* ÊàëÁöÑÊµÅÂä®ÊÄß */}
                  <MyLiquidity
                    lpTokenInfo={lpTokenInfo}
                    sharePoolRate={currentVaultPositionLoading ? '' : currentVaultPositionInfo?.shartOfPoolDisplay || 0}
                    balanceDisplay={currentVaultPositionLoading ? '' : currentVaultPositionInfo?.balanceDisplay || 0}
                    totalHolding={currentVaultPositionLoading ? '' : holdingAmountDisplay || ''}
                    displayTokenB={apiVaultInfo?.displayTokenB}
                    displayTokenA={apiVaultInfo?.displayTokenA}
                    holdCoinAmounts={{
                      displayCoinA: currentVaultPositionLoading ? '' : formatNumber(currentVaultPositionInfo?.displayAmountA, 6) || '--',
                      displayCoinB: currentVaultPositionLoading ? '' : formatNumber(currentVaultPositionInfo?.displayAmountB, 6) || '--'
                    }}
                    holdCoinAValue={holdCoinAValue}
                    holdCoinBValue={holdCoinBValue}
                  />
                </>
              ) : (
                <>
                  <TvlChartPageBlock isRefresh={chartRefresh} vaultsId={apiVaultInfo?.vaultId} />
                  <ApyAndFeesChartBlock isRefresh={chartRefresh} vaultsId={apiVaultInfo?.vaultId} />
                  <PriceRangeChartPageBlock isRefresh={chartRefresh} vaultsId={apiVaultInfo?.vaultId} isReverse={apiVaultInfo?.isReverse} />
                </>
              )}
            </VStack>
            <VStack w={isApp ? '100%' : '40%'}>
              <VaultsAction vaultId={vaultId as string} autoRefresh={autoRefresh} isModal={false} />
            </VStack>
          </Stack>
        </VStack>
      ) : (
        <NoData type="custom" imgUrl="/images/img_create_pool.png" h="480px">
          <VStack>
            <Text h="20px" lineHeight="20px" fontSize="14px" color="text_caption">
              Pool address not found
            </Text>
            <Button
              mt="12px"
              w="120px"
              h="32px"
              borderRadius="8px"
              colorScheme="blue"
              fontSize="14px"
              fontWeight="500"
              onClick={() => {
                navigate('/vaults')
              }}
            >
              Back to Vault
            </Button>
          </VStack>
        </NoData>
      )}
    </Box>
  )
}
