import useDisplayPrice from '@/hooks/common/useDisplayPrice'
import useDisplayTickPrice from '@/hooks/common/useDisplayTickPrice'
import { Block } from '@cetus/design'
import { Token } from '@cetus/types'
import { Icon, PositionPriceRange, SingleCoinImage } from '@cetus/ui-kit'
import { HStack, Text, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'

// VaultËØ¶ÊÉÖ ‰ª∑Ê†ºËåÉÂõ¥
export function PositionRange({
  currentPrice,
  minPrice,
  maxPrice,
  displayTokenA,
  displayTokenB
}: {
  currentPrice?: string
  minPrice?: string
  maxPrice?: string
  displayTokenA?: Token
  displayTokenB?: Token
}) {
  const [changeSide, setChangeSide] = useState<boolean>(false)
  const { displayPrice } = useDisplayPrice({ price: currentPrice, changeSide })
  const { displayMaxPrice, displayMinPrice } = useDisplayTickPrice({ minPrice, maxPrice, changeSide })

  const baseCoin = useMemo(() => {
    return changeSide ? displayTokenA : displayTokenB
  }, [displayTokenA, displayTokenB, changeSide])

  const logoUrl = useMemo(() => {
    return baseCoin?.logo_url ? baseCoin?.logo_url : ''
  }, [baseCoin?.logo_url])

  return (
    <Block bg="bg_six" borderColor="border_secondary" p={{ base: '16px 8px', lg: '20px 16px' }} position="relative" height="290px">
      <VStack w="100%">
        <HStack w="100%" justifyContent="space-between" mb="6px">
          <Text fontSize="16px" color="text_caption">
            Position Range
          </Text>
          {/* ÂàáÊç¢base */}
          <HStack gap="0px">
            {logoUrl && (
              <SingleCoinImage
                imageUrl={logoUrl}
                imageStyle={{
                  w: '20px',
                  h: '20px'
                }}
              />
            )}
            <Text color="text_caption"> {baseCoin?.symbol}</Text>

            <HStack ml="8px" gap="4px" cursor="pointer" onClick={() => setChangeSide(!changeSide)}>
              <Text fontSize="12px"> Base coin</Text>
              <Icon xlinkHref="#icon-icon_swap1" />
            </HStack>
          </HStack>
        </HStack>

        <PositionPriceRange displayPrice={displayPrice} displayMaxPrice={displayMaxPrice} displayMinPrice={displayMinPrice} />
      </VStack>
    </Block>
  )
}
