import { Block } from '@cetus/design'
import SingleTokenInfo from '@cetus/design/src/components/common/SingleTokenInfo'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { Token } from '@cetus/types'
import { VTextLabelBox } from '@cetus/ui-kit'
import { formatCurrency, formatNumber, symbolDataDisplayProcessing, textEllipses } from '@cetus/utils'
import { HStack, Progress, Text, VStack } from '@chakra-ui/react'
import { AumLimit } from '../common/AumLimit'

// VaultËØ¶ÊÉÖ Ê±†Â≠êÊµÅÂä®ÊÄßÁªÑÊàê
export function VaultsComposition({
  displayAmountA,
  displayAmountB,
  displayPercentA,
  displayPercentB,
  displayTokenA,
  displayTokenB,
  vaultsCoinAValue,
  vaultsCoinBValue,
  depositRatio,
  hardCapUSD,
  vaultTvl
}: {
  displayAmountA?: string
  displayAmountB?: string
  displayPercentA?: string
  displayPercentB?: string
  displayTokenA?: Token
  displayTokenB?: Token
  vaultsCoinAValue?: string
  vaultsCoinBValue?: string
  depositRatio?: string
  hardCapUSD?: string
  vaultTvl?: string
}) {
  const { isApp } = useWindowWidth()

  return (
    <Block bg="bg_six" borderColor="border_secondary" p={{ base: '16px 8px', lg: '20px 16px' }}>
      <VStack w="100%" gap="0px">
        <HStack w="100%" justifyContent="space-between">
          <Text w="100%" textAlign="start" fontSize="16px" color="text_caption">
            Vault Composition
          </Text>
          {hardCapUSD && (
            <AumLimit
              depositRatio={depositRatio}
              hardCapUSD={hardCapUSD}
              vaultTvl={vaultTvl}
              label="Capacity"
              value={symbolDataDisplayProcessing(hardCapUSD, '$')}
            />
          )}
        </HStack>
        {/* ÁôæÂàÜÊØî  */}
        <HStack w={{ base: '100%', lg: '90%' }} justifyContent="space-between" mt="28px">
          <HStack>
            <SingleTokenInfo
              token={displayTokenA}
              haveName={false}
              haveSymbol={false}
              symbolFontSize="12px"
              warningIcon={{ iconW: '16px', iconH: '16px' }}
              imgBoxStyle={{ w: '32px', h: '32px' }}
              haveTooltip={!isApp}
            />
            <VTextLabelBox
              wrapStyle={{
                gap: '4px'
              }}
              title={textEllipses(displayTokenA?.symbol, 10) || ''}
              titleStyle={{
                fontSize: '16px',
                color: 'text_caption',
                fontWeight: '600'
              }}
              value={`${displayPercentA}%`}
              isLoading={!displayPercentA}
              valueStyle={{
                fontSize: '12px',
                color: 'text_paragraph',
                fontWeight: '500'
              }}
            />
          </HStack>

          <Progress
            ml="16px"
            mr="16px"
            flex="1"
            h="4px"
            value={Number(displayPercentA)}
            bg={Number(displayPercentA) !== 0 && Number(displayPercentB) !== 0 ? 'quote_green' : 'process_bg_gray'}
            sx={{
              'div[role="progressbar"]': {
                bg: 'primary'
              }
            }}
          />
          <HStack>
            <VTextLabelBox
              wrapStyle={{
                gap: '4px',
                alignItems: 'flex-end'
              }}
              title={textEllipses(displayTokenB?.symbol, 10) || ''}
              titleStyle={{
                fontSize: '16px',
                color: 'text_caption',
                fontWeight: '600'
              }}
              value={`${displayPercentB}%`}
              isLoading={!displayPercentB}
              valueStyle={{
                fontSize: '12px',
                color: 'text_paragraph',
                fontWeight: '500'
              }}
            />

            <SingleTokenInfo
              token={displayTokenB}
              haveName={false}
              haveSymbol={false}
              symbolFontSize="12px"
              warningIcon={{ iconW: '16px', iconH: '16px' }}
              imgBoxStyle={{ w: '32px', h: '32px' }}
              haveTooltip={true}
            />
          </HStack>
        </HStack>
        {/* Êï∞Èáè/‰ª∑ÂÄº */}
        <HStack w={{ base: '100%', lg: '90%' }} justifyContent="space-between" mt="12px">
          <VTextLabelBox
            wrapStyle={{
              gap: { base: '8px', lg: '4px' }
            }}
            title={`${formatNumber(displayAmountA, 6)} ${textEllipses(displayTokenA?.symbol, 10)}`}
            isTitleLoading={!displayAmountA}
            titleStyle={{
              fontSize: { base: '12px', lg: '14px' },
              color: 'text_caption',
              fontWeight: '500'
            }}
            value={formatCurrency(vaultsCoinAValue, 2)}
            isLoading={!vaultsCoinAValue}
            valueStyle={{
              color: 'text_paragraph',
              fontSize: { base: '12px', lg: '14px' },
              fontWeight: '500'
            }}
          />

          <VTextLabelBox
            wrapStyle={{
              gap: { base: '8px', lg: '4px' },
              alignItems: 'flex-end'
            }}
            title={`${formatNumber(displayAmountB, 6)} ${textEllipses(displayTokenB?.symbol, 10)}`}
            isTitleLoading={!displayAmountB}
            titleStyle={{
              fontSize: { base: '12px', lg: '14px' },
              color: 'text_caption',
              fontWeight: '500'
            }}
            value={formatCurrency(vaultsCoinBValue, 2)}
            isLoading={!vaultsCoinBValue}
            valueStyle={{
              color: 'text_paragraph',
              fontSize: { base: '12px', lg: '14px' },
              fontWeight: '500'
            }}
          />
        </HStack>
      </VStack>
    </Block>
  )
}
